import React from 'react';
import { OutboundLink } from 'gatsby-plugin-google-gtag';
import { graphql, useStaticQuery } from 'gatsby';
import {
  faCodepen,
  faGithub,
  faInstagram,
  faLinkedin,
  faMediumM,
  faStackOverflow,
  faTwitter,
} from '@fortawesome/free-brands-svg-icons';
import styled from 'styled-components';
import { TSocialsBorder, TSocialsIcon } from './ThemedComponents';

interface SocialsProp {
  isNav?: boolean;
}

const Socials: React.FC<SocialsProp> = ({ isNav }: SocialsProp) => {
  const {
    site: {
      siteMetadata: { socials },
    },
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          socials {
            twitter
            linkedin
            github
            stackoverflow
            medium
            codepen
            instagram
          }
        }
      }
    }
  `);

  const SSocialsContainer = styled.div<SocialsProp>`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: auto;

    margin: 15px 1px;
    @media only screen and (max-width: 1000px) {
      max-width: 400px;
      width: 400px;
    }
    @media only screen and (min-width: 1000px) {
      max-width: ${props => (props.isNav ? '200px' : '400px')};
      width: ${props => (props.isNav ? '200px' : '400px')};
    }
  `;

  const SSocialsBorder = styled(TSocialsBorder)`
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35px;
    width: 35px;
    border-radius: 50%;
    margin: 5px 5px;
    background-color: white;
    z-index: 0;
  `;

  const SSocialsIcon = styled(TSocialsIcon)`
    font-size: larger;
    margin: 3px 3px;
    z-index: 1;
  `;

  return (
    <SSocialsContainer isNav={isNav}>
      <OutboundLink href={socials.github}>
        <SSocialsBorder>
          <SSocialsIcon icon={faGithub} />
        </SSocialsBorder>
      </OutboundLink>
      <OutboundLink href={socials.linkedin}>
        <SSocialsBorder>
          <SSocialsIcon icon={faLinkedin} />
        </SSocialsBorder>
      </OutboundLink>
      <OutboundLink href={socials.medium}>
        <SSocialsBorder>
          <SSocialsIcon icon={faMediumM} />
        </SSocialsBorder>
      </OutboundLink>
      <OutboundLink href={socials.stackoverflow}>
        <SSocialsBorder>
          <SSocialsIcon icon={faStackOverflow} />
        </SSocialsBorder>
      </OutboundLink>
      <OutboundLink href={socials.codepen}>
        <SSocialsBorder>
          <SSocialsIcon icon={faCodepen} />
        </SSocialsBorder>
      </OutboundLink>
      <OutboundLink href={socials.twitter}>
        <SSocialsBorder>
          <SSocialsIcon icon={faTwitter} />
        </SSocialsBorder>
      </OutboundLink>
      <OutboundLink href={socials.instagram}>
        <SSocialsBorder>
          <SSocialsIcon icon={faInstagram} />
        </SSocialsBorder>
      </OutboundLink>
    </SSocialsContainer>
  );
};

Socials.defaultProps = {
  isNav: false,
};

export default Socials;
